import { mapState, mapMutations } from 'vuex'
import ListResume from '@/components/ListResume/ListResume.vue'
import Search from '@/components/Search/Search.vue' 
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import Dialog from '@/components/Dialog/Dialog.vue'
import ResumeModel from '@/models/resume.js'
const resumeModel = new ResumeModel()
export default {
	data() {
		return {
			resumeList: [
				{
					type: 'started'
				},
				{
					type: 'notStart'
				}
			],
			type: 'all',
			pageNum: 1,
			pageSize: 10,
			hasEnd: false,
			showDialog: false,
			recommendRecord: [],
			resumeId: '',
			date: '',
			timeStart: '',
			timeEnd: '',
			formId: '' , // 推送相关
			keyWord:''
		}
	},
	components:{
		ListResume,
		Search,
		uniPopup,
		Dialog
	},
	computed:{
		...mapState(['currentResume', 'jobId']),
		
	},
	mounted(){
		this.getResumeList()
	},
	methods: {
		...mapMutations(['switchTab']),
		search(keyWord){
			// 做搜索动作
			console.log(keyWord)
			this.keyWord = keyWord
			this.getResumeList()
		},
		getResumeList(){
			const type = this.type
			let status = ''
			if(type === 'started'){
				status = 1
			}else if(type === 'notStart'){
				status = 0
			}
			resumeModel.resumeList(this.pageNum,this.pageSize,status,this.keyWord).then(res=>{
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					if(this.pageNum === 1){
						that.resumeList = data
					}else {
						that.resumeList = [...that.resumeList, ...data]
					}
					if(data.length < that.pageSize){
						that.hasEnd = true
					}
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取简历信息失败'
				})
			})
		},
		refresh(){
			this.pageSize = 1
			this.hasEnd = false
			this.getResumeList()
		},
		getNext(){
			if(!this.hasEnd){
				this.pageNum += 1
				this.getResumeList()
			}
		},
		switchResumeTab(type){
			if(type === this.type){
				return 
			}
			this.type = type
			this.pageNum = 1
			this.hasEnd = false
			this.getResumeList()
		},
		showRecord(id){
			uni.showLoading({
				mask:true
			})
			resumeModel.resumeRecord(this.pageSize, this.pageNum,id).then(res=>{
				uni.hideLoading()
				this.$refs.recommendRecord.open()
				const { code, message, data} = res.data
				if(code === '0'){
					this.recommendRecord = data
					
				}else {
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取推荐记录失败'
				})
			})
		},
		closeRecommendDialog(){
			this.$refs.recommendRecord.close()
		},
		showDetail(id){
			uni.navigateTo({
				url: '../../public/addResume/addResume?isEdit=true&id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		recommendHim(id){
			const jobId = this.jobId
			this.resumeId = id
			if(jobId){
				this.$refs.selectDate.open()
			}else{
				this.switchTab({index:1,resumeId: this.resumeId})
			}
		},	
		pushResume(){
			const that = this
			const jobId = this.jobId
			const resumeId = this.resumeId
			const interviewDate = this.date
			const formId = this.formId
			const interviewTime = this.timeStart + this.timeEnd
			uni.showLoading({
				title: '提交中...'
			})
			resumeModel.pushResume(jobId, resumeId, interviewDate, interviewTime, formId).then(res=>{
				const { code, message, data } = res.data
				that.formId = ''
				uni.hideLoading()
				if(code === '0'){
					// 推荐成功
					uni.showModal({
						title: '',
						content: '推荐成功!\r\n您可在“我的项目”栏目查看该人员的面试、入职流程',
						showCancel: false,
						confirmText: '知道了',
						success: res => {},
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				that.formId = ''
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title:'推送失败'
				})
			})
		},
		cancelPush(){
			this.$refs.selectDate.close()
		},
		showAddResume(){
			uni.navigateTo({
				url: '../../public/addResume/addResume?isEdit=true',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		bindDateChange(e){
			this.date = e.target.value
		},
		bindTimeStartChange(e){
			this.timeStart = e.target.value
		},
		bindTimeEndChange(e){
			this.timeEnd = e.target.value
		},
		getFormId(e){
			this.formId = e.detail.formId
		}
	}
}
